.tooltip {
  min-width: 150px;
  text-align: center;
  color: #fff;
  filter: drop-shadow(4px 4px 4px #29324166);
  margin-left: -40px;
  padding: 1em;
  font-weight: 700;
  display: block;
  position: absolute;
}

.tooltip:before {
  content: "";
  width: 20px;
  height: 20px;
  clip-path: polygon(0 100%, 0 0, 100% 0);
  position: absolute;
  top: 100%;
  left: 40px;
}

.tooltip.left:before {
  left: unset;
  clip-path: polygon(0 0, 100% 0, 100% 100%);
  right: 40px;
}

.tooltip.info {
  color: #293241;
  background-color: #fff;
}

.tooltip.info:before {
  background-color: #fff;
}

.tooltip.success, .tooltip.success:before {
  background-color: #2ecc71;
}

.tooltip.error, .tooltip.error:before {
  background-color: #d14545;
}

.tooltip.show {
  animation: .15s ease-out anim-tooltip;
}

@keyframes anim-tooltip {
  from {
    opacity: 0;
  }
}

